.contact {
  --textcolorviolet: rgb(97, 26, 74);

  position: fixed;
  inset: 0;
  z-index: 1001;
  background-color: rgba(0, 0, 0, 0.2);

  &__container {
    width: 800px;
    height: fit-content;
    margin: 100px auto;
    background-color: var(--violetcolor);
    border: 0.2rem solid var(--textcolorviolet);
    border-radius: 20px;

    &__form {
      width: 100%;
      padding: 0 30px;

      &__closebtn {
        width: 100%;
        display: flex;
        flex-direction: row-reverse;
        font-size: 30px;

        span {
          color: var(--textcolorviolet);
        }
      }

      &__name {
        width: 100%;
        height: 40px;
        display: flex;
        justify-content: space-between;
        margin-bottom: 5px;

        input {
          width: 49%;
          border: 0.1rem solid var(--textcolorviolet);
          background-color: var(--yellowcolor);
          color: var(--textcolorviolet);
          padding: 0 5px;
          text-transform: uppercase;
        }

        input[type="text"]:focus {
          outline-width: 0;
        }

        input::placeholder {
          color: var(--textcolorviolet);
          text-transform: uppercase;
        }
      }

      &__contacts {
        width: 100%;
        height: 40px;
        display: flex;
        justify-content: space-between;
        margin-bottom: 5px;

        input {
          width: 49%;
          border: 0.1rem solid var(--textcolorviolet);
          background-color: var(--yellowcolor);
          color: var(--textcolorviolet);
          padding: 0 5px;
          text-transform: uppercase;
        }

        input[type="email"]:focus {
          outline-width: 0;
        }

        input[type="email"]::placeholder {
          color: var(--red);
        }

        input[type="number"]:focus {
          outline-width: 0;
        }

        input::placeholder {
          color: var(--textcolor);
          text-transform: uppercase;
        }
      }

      &__subject {
        width: 100%;
        margin-bottom: 5px;

        input {
          height: 40px;
          width: 100%;
          border: 0.1rem solid var(--textcolorviolet);
          background-color: var(--yellowcolor);
          color: var(--textcolorviolet);
          padding: 0 5px;
          text-transform: uppercase;
        }

        input[type="text"]:focus {
          outline-width: 0;
        }

        input::placeholder {
          color: var(--textcolorviolet);
          text-transform: uppercase;
        }
      }

      &__message {
        width: 100%;
        margin-bottom: 5px;
        height: fit-content;

        textarea {
          min-height: 150px;
          width: 100%;
          border: 0.1rem solid var(--textcolorviolet);
          background-color: var(--yellowcolor);
          color: var(--textcolorviolet);
          padding: 5px;
          text-transform: uppercase;
          font-size: 16px;
          resize: none;
        }

        textarea[type="text"]:focus {
          outline-width: 0;
          height: auto;
        }

        textarea::placeholder {
          color: var(--red);
          text-transform: uppercase;
        }
      }

      &__buttons {
        width: 100%;
        display: flex;
        justify-content: center;
        margin-bottom: 5px;
        padding: 5px;

        input {
          height: 30px;
          width: 60px;
          border: 0.1rem solid var(--textcolorviolet);
          background-color: var(--yellowcolor);
          color: var(--textcolorviolet);
          padding: 0 5px;
          text-transform: uppercase;
        }
      }
    }
  }
}

@keyframes appear {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes disappear {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

.showModal {
  animation: appear 1s;
}

.hideModal {
  animation: disappear 1s;
}

@media only screen and (max-width: 900px) {
  .contact {
    &__container {
      width: 80%;
      height: 600px;


      &__form {
        display: flex;
        flex-wrap: wrap;
        flex-direction: column;
        justify-content: space-between;
        width: fit-content;
        height: 100%;
        padding: 0 10px;


        &__name {
          flex-wrap: wrap;
          height: 100px;

          input {
            width: 100%;
            font-size: 20px;
          }
        }

        &__contacts {
          flex-wrap: wrap;
          height: 100px;

          input {
            width: 100%;
            font-size: 20px;
          }
        }

        &__subject {
          height: 50px;

          input {
            height: 100%;
            font-size: 20px;
          }
        }

        &__message {
          textarea {
            font-size: 20px;
          }
        }

        &__buttons{
          input{
            height: 60px;
            width: 120px;
            font-size: 20px;
          }
        }
      }
    }
  }
}