.recievedFeedback {
  width: 100%;
  min-width: 1100px;
  height: fit-content;
  background-color: transparent;
  margin-top: 30px;

  &__wrapper {
    width: 100%;
    height: fit-content;
    margin: 0 auto;
    padding: 30px 0;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;

    h3 {
      display: block;
      font-size: 30px;
      line-height: 30px;
      text-transform: uppercase;
      color: var(--violetcolor);
      margin: 0;
    }

    &__comment {
      width: 80%;
      height: fit-content;
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      justify-content: flex-start;
      margin-right: 140px;

      figure {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        img {
          height: 70px;
          width: auto;
        }

        figcaption {}
      }


      input {
        display: flex;
        justify-content: flex-start;
        padding: 0 4px;
        font-size: 18px;
        line-height: 20px;
        flex-grow: 2;
        width: auto;
        min-height: 100px;
        height: fit-content;
        border-radius: 10px;
        border: 2px dashed var(--violetcolor);
        background-color: var(--absolutewhitecolor);
      }

      input:focus {
        outline: none;
        height: auto;
      }
    }
  }
}

@media only screen and (max-width: 1300px) {
  .recievedFeedback {
    min-width: 100%;

    &__wrapper {
      h3 {
        font-size: 25px;
      }

      &__comment{
        margin: 0;
        justify-content: center;

        figure{
          img{
            height: 30%;
          }
        }

        input{
          min-height: 50px;
        }
      }
    }

  }
}