* {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*::-webkit-scrollbar {
  width: 12px;
}

*::-webkit-scrollbar-track {
  background: #c879f4;
}

*::-webkit-scrollbar-thumb {
  background-color: #f4dd89;
  border-radius: 20px;
  border: 3px solid #ffffff;
}

body {
  font-family: "Changa", sans-serif;
  color: rgb(22, 22, 22);
  background-color: rgb(255 187 187);
  margin: 0;
  scroll-behavior: smooth;
  cursor: url("./assets/cursor/cursor-for-body.png"), auto;
  --whitecolor: azure;
  --absolutewhitecolor: rgba(255, 255, 255, 1);
  --violetcolor: #cc7cf3;
  --lightpinkcolor: #ffdada;
  --pinkgradientcolor: rgba(255, 187, 187, 1);
  --brightpinkcolor:  rgba(255, 170, 170, 1);
  --peachcolor: #ffbbbb;
  --yellowcolor: rgba(244, 220, 128, 1);
  --blackcolor: rgb(22, 22, 22);
  --red: red;
}

a {
  text-decoration: none;
}

a:hover {
  cursor: url("./assets/cursor/cursor-for-link.png"), auto;
}

button {
  cursor: url("./assets/cursor/cursor.png"), auto;
}
input:hover {
  cursor: url("./assets/cursor/cursor.png"), auto;
}
